/* ==========================================================================
# Pop-up style - begin
========================================================================== */
%popup-input-style {
    font-size: 12px;
    box-sizing: border-box;
    display: inline-block;
    margin-bottom: 10px;
    vertical-align: top;
    height: 40px;
    line-height: 40px;
    background-color: transparent;
    color: #000000;
    border: 1px solid #dbdbdb;

    &:focus {

        &::-webkit-input-placeholder { /* WebKit, Blink, Edge */
            color: #000000;
        }
        &:-moz-placeholder { /* Mozilla Firefox 4 to 18 */
            color: #000000;
            opacity: 1;
        }
        &::-moz-placeholder { /* Mozilla Firefox 19+ */
            color: #000000;
            opacity: 1;
        }
        &:-ms-input-placeholder { /* Internet Explorer 10-11 */
            color: #000000;
            line-height: normal;
        }
    }
    &::-webkit-input-placeholder { /* WebKit, Blink, Edge */
        color: #000000;
    }
    &:-moz-placeholder { /* Mozilla Firefox 4 to 18 */
        color: #000000;
        opacity: 1;
    }
    &::-moz-placeholder { /* Mozilla Firefox 19+ */
        color: #000000;
        opacity: 1;
    }
    &:-ms-input-placeholder { /* Internet Explorer 10-11 */
        color: #000000;
        line-height: normal;
    }
}

/* Popup opener style - start */

.qode-popup-opener {
    vertical-align: middle;
    display: inline-block;
}

.widget_qode_popup_opener{
    display: inline-block;
    vertical-align: middle;
}


/* Popup opener style - end */

.qode-popup-holder {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    -webkit-transition: opacity .2s ease-out .1s, visibility .0s .4s;
    transition: opacity .2s ease-out .1s, visibility .0s .4s;
    z-index: 115;
    visibility: hidden;

    .qode-popup-shader {
        top: 0;
        left: 0;
        position: fixed;
        width: 110%;
        height: 130%;
        z-index: 1;
        -webkit-transform: translateZ(0);
        transform: translateZ(0);
        -webkit-transform-style: flat;
        transform-style: flat;
        backface-visibility: hidden;
        background-color: rgba(0, 0, 0, 0.65);
    }

    .qode-popup-table {
        display: table;
        width: 100%;
        height: 100%;
        position: relative;
        z-index: 9;
    }

    .qode-popup-table-cell {
        display: table-cell;
        vertical-align: middle;
        width: 100%;
        height: 100%;
        text-align: center;
    }

    .qode-popup-inner {
        display: inline-block;
        width: 43%;
        background-color: #fff;
        box-sizing: border-box;
        @include qodeTransform(scale(.85));
        opacity: 0;
        @include qodeTransition(transform .3s cubic-bezier(0.06, 0.47, 0.38, 0.99), opacity .15s cubic-bezier(0.06, 0.47, 0.38, 0.99));
    }

    .qode-popup-close {
        display: inline-block;
        font-size: 32px;
        color: #787878;
        position: absolute;
        top: 25px;
        right: 28px;
    }

    .qode-popup-content-container {
        background: #fff;
        padding: 80px 110px 90px;
    }

    .qode-popup-title-holder {
        margin-bottom: 16px;

        .qode-popup-title {
            font-weight: 600;
            line-height: 1.11em;
            margin: 0;
        }
    }

    .qode-popup-subtitle {
        font-size: 13px;
        margin-bottom: 33px;
    }

    .wpcf7 {
        position: relative;
        width: 100%;
        display: block;
        margin: 0 auto;

        label{
            text-align: left;
            display: block;
        }
    }
    .wpcf7-form-control {
        &.wpcf7-text,
        &.wpcf7-number,
        &.wpcf7-date,
        &.wpcf7-textarea,
        &.wpcf7-select,
        &.wpcf7-quiz {
            border: 1px solid #d6d6d6;
        }

        &.wpcf7-submit{
            margin-top: 15px;
        }
    }

    .wpcf7-response-output{
        color: inherit;
        padding: 0;
        margin: 45px 0 0;

        &:before{
            display: none;
        }
    }

}

/* Popup open/close style */

.qode-popup-opened {
    .qode-popup-holder {
        opacity: 1;
        visibility: visible;
        -webkit-transition: opacity .3s ease;
        transition: opacity .3s ease;
        .qode-popup-inner {
            opacity: 1;
            @include qodeTransform(scale(1));
            @include qodeTransition(transform .5s cubic-bezier(0.26, 0.63, 0, 0.96));
        }
    }
}

/* ==========================================================================
# Pop-up style - end
========================================================================== */